










































































*{
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
  width: 100%;
 
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  width: 100%;
  text-align: center;
}
/* app组件 */
#app {
  color: #2c3e50;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  width: 70%;
  height: 70%;
  position: relative;
  top: 0px;
  text-align: left;
}
/*list完成后添加删除线样式 */
.finish{
  text-decoration: line-through;
}
#app .titled{
  position: absolute;
  top: -50px;
}
.inputbox{
  width: 100%;
  height: 30px;
  margin-bottom: 20px;
}

.lis{
  cursor: pointer;
  
}
ul{
  text-align: left;
  list-style: none;
  line-height: 40px;
}
li{
  font-size: 20px;
  font-weight: bold;
  display: block;
}
p,.del{
  display: none;
}
h3{
  display: inline;
}
li:hover .del{
display: inline;
text-decoration: underline;
font-size: 12px;
color: grey;
cursor: pointer;
}
button{
  cursor: pointer;
}
.item-status{
  display: inline;
  background: red;
  color: white;
  padding: 0 5px;
  font-size: 12px;
}



